import React, { useState } from 'react';
import './FutureYearForm.css';
import TaxRates from '../../utils/TaxRates';
import { ISTISNA } from '../../utils/calculateSalary';

const FutureYearForm = ({ onSubmit, onClose, initialValues = null, savedYears = {} }) => {
    const lastYear = TaxRates.getLastYear();

    // Eer initialValues varsa onlar kullan, yoksa default deerler (son yl zerinden + 1)
    const [year, setYear] = useState(initialValues?.year || (lastYear + 1));
    const [inflation, setInflation] = useState(initialValues?.inflation || '');
    const [baseYear, setBaseYear] = useState(''); // Hangi yl baz alacaz

    // Son yln verilerini al
    const ratesLastYear = TaxRates.getRates(lastYear);

    // En yksek yl olan 2025'in ISTISNA deerlerini al ve %25.49 artr
    const latestYearKey = Math.max(...Object.keys(ISTISNA).map(Number));
    const exemptionsSource = ISTISNA[latestYearKey];

    // %25.49 art uygula
    const increaseRate = 1.2549;
    const exemptionsCalculated = {
        gelir: exemptionsSource.gelir.map(v => Math.round(v * increaseRate * 100) / 100),
        damga: exemptionsSource.damga.map(v => Math.round(v * increaseRate * 100) / 100)
    };

    const [taxRates, setTaxRates] = useState(
        initialValues?.taxRates || ratesLastYear
    );

    const [exemptions, setExemptions] = useState(
        initialValues?.exemptions || exemptionsCalculated
    );

    // Baz yl seildiinde o yln verilerini ykle
    const handleBaseYearChange = (selectedYear) => {
        setBaseYear(selectedYear);

        if (selectedYear && savedYears[selectedYear]) {
            const baseData = savedYears[selectedYear];
            setTaxRates(baseData.taxRates);
            setExemptions(baseData.exemptions);
            setInflation(''); // Enflasyon alann bo brak, kullanc yeni deer girecek
        } else if (selectedYear === lastYear.toString()) {
            // Default deerleri ykle
            setTaxRates({
                sgk: 0.14,
                issizlik: 0.01,
                damga: 0.00759,
                gelir: ratesLastYear.gelir
            });
            setExemptions({
                gelir: exemptionsCalculated.gelir,
                damga: exemptionsCalculated.damga
            });
            setInflation('');
        }
    };

    // Enflasyon oranna gre otomatik doldur
    const handleInflationChange = (value) => {
        setInflation(value);

        if (value && !isNaN(value)) {
            const inflationRate = parseFloat(value) / 100;

            // Hangi yl baz alyoruz?
            let baseRates, baseExemptions;

            if (baseYear && savedYears[baseYear]) {
                baseRates = savedYears[baseYear].taxRates;
                baseExemptions = savedYears[baseYear].exemptions;
            } else {
                // Default deerleri
                baseRates = ratesLastYear;
                baseExemptions = exemptionsCalculated;
            }

            // Vergi dilimlerini enflasyon oranna gre gncelle
            const updatedGelir = baseRates.gelir.map(dilim => ({
                ...dilim,
                min: Math.round(dilim.min * (1 + inflationRate)),
                max: dilim.max === Infinity ? 999999999 : Math.round(dilim.max * (1 + inflationRate))
            }));

            setTaxRates({
                ...baseRates,
                gelir: updatedGelir
            });

            // stisnalar enflasyon oranna gre gncelle
            setExemptions({
                gelir: baseExemptions.gelir.map(val => Math.round(val * (1 + inflationRate) * 100) / 100),
                damga: baseExemptions.damga.map(val => Math.round(val * (1 + inflationRate) * 100) / 100)
            });
        }
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        onSubmit({
            year,
            taxRates,
            exemptions,
            inflation
        });
        onClose();
    };

    const updateGelirDilim = (index, field, value) => {
        const updatedGelir = [...taxRates.gelir];
        updatedGelir[index] = {
            ...updatedGelir[index],
            [field]: field === 'oran' ? parseFloat(value) / 100 : parseInt(value)
        };
        setTaxRates({
            ...taxRates,
            gelir: updatedGelir
        });
    };

    const updateExemption = (type, index, value) => {
        setExemptions({
            ...exemptions,
            [type]: exemptions[type].map((val, i) => i === index ? parseFloat(value) : val)
        });
    };

    return (
        <div className="future-year-form-overlay">
            <div className="future-year-form">
                <div className="form-header">
                    <h2>{initialValues ? 'Vergi Oranlarn Dzenle' : 'Gelecek Yl Hesaplama Ayarlar'}</h2>
                    <button className="close-btn" onClick={onClose}></button>
                </div>

                <form onSubmit={handleSubmit}>
                    <div className="form-section">
                        <label>
                            Hesaplama Yl:
                            <input
                                type="number"
                                value={year}
                                onChange={(e) => setYear(parseInt(e.target.value))}
                                min="2025"
                                max="2050"
                            />
                        </label>
                    </div>

                    {/* Baz yl seme - sadece yeni yl eklerken grn */}
                    {!initialValues && Object.keys(savedYears).length > 0 && (
                        <div className="form-section">
                            <label>
                                Hangi yln verilerini baz almak istiyorsunuz?
                                <select
                                    value={baseYear}
                                    onChange={(e) => handleBaseYearChange(e.target.value)}
                                >
                                    <option value="">{lastYear} (Varsaylan deerler)</option>
                                    {Object.keys(savedYears).sort().map(year => (
                                        <option key={year} value={year}>
                                            {year} ({savedYears[year].inflation ? `%${savedYears[year].inflation} enflasyon` : 'zel deerler'})
                                        </option>
                                    ))}
                                </select>
                            </label>
                            <small>Seilen yln vergi oranlar ve istisnalar balang noktas olacak</small>
                        </div>
                    )}

                    <div className="form-section">
                        <label>
                            Enflasyon Oran (%) - stee Bal:
                            <input
                                type="number"
                                value={inflation}
                                onChange={(e) => handleInflationChange(e.target.value)}
                                placeholder="rn: 30"
                                step="0.1"
                            />
                        </label>
                        <small>{baseYear ? `${baseYear} ylnn deerlerini bu oranda arttrr` : `${lastYear} deerlerini otomatik olarak bu oranda arttrr`}</small>
                    </div>

                    <div className="form-section">
                        <h3>Gelir Vergisi Dilimleri</h3>
                        {taxRates.gelir.map((dilim, index) => (
                            <div key={index} className="dilim-row">
                                <input
                                    type="number"
                                    value={dilim.min}
                                    onChange={(e) => updateGelirDilim(index, 'min', e.target.value)}
                                    placeholder="Min"
                                />
                                <span>-</span>
                                <input
                                    type="number"
                                    value={dilim.max === 999999999 ? '' : dilim.max}
                                    onChange={(e) => updateGelirDilim(index, 'max', e.target.value || 999999999)}
                                    placeholder="Max (bo = snrsz)"
                                />
                                <input
                                    type="number"
                                    value={dilim.oran * 100}
                                    onChange={(e) => updateGelirDilim(index, 'oran', e.target.value)}
                                    placeholder="Oran %"
                                    step="0.1"
                                />
                                <span>%</span>
                            </div>
                        ))}
                    </div>

                    <div className="form-section">
                        <h3>{lastYear} Aylk Gelir Vergisi stisnalar</h3>
                        <div className="exemption-grid">
                            {['Ocak', 'ubat', 'Mart', 'Nisan', 'Mays', 'Haziran',
                                'Temmuz', 'Austos', 'Eyll', 'Ekim', 'Kasm', 'Aralk'].map((month, index) => (
                                    <div key={index} className="exemption-item">
                                        <label>{month}:</label>
                                        <input
                                            type="number"
                                            value={exemptions.gelir[index]}
                                            onChange={(e) => updateExemption('gelir', index, e.target.value)}
                                            step="0.01"
                                        />
                                    </div>
                                ))}
                        </div>
                    </div>

                    <div className="form-section">
                        <h3>{lastYear} Aylk Damga Vergisi stisnalar</h3>
                        <div className="exemption-grid">
                            {['Ocak', 'ubat', 'Mart', 'Nisan', 'Mays', 'Haziran',
                                'Temmuz', 'Austos', 'Eyll', 'Ekim', 'Kasm', 'Aralk'].map((month, index) => (
                                    <div key={index} className="exemption-item">
                                        <label>{month}:</label>
                                        <input
                                            type="number"
                                            value={exemptions.damga[index]}
                                            onChange={(e) => updateExemption('damga', index, e.target.value)}
                                            step="0.01"
                                        />
                                    </div>
                                ))}
                        </div>
                    </div>

                    <div className="form-actions">
                        <button type="button" onClick={onClose}>ptal</button>
                        <button type="submit">
                            {initialValues ? 'Gncellemeleri Uygula' : 'Hesaplamay Balat'}
                        </button>
                    </div>
                </form>
            </div>
        </div>
    );
};

export default FutureYearForm; 